#name-label {
  padding: 20px;
  padding-top: 10px;
  color: rgba(11, 11, 11, 0.8);
  font-family: "Poppins", sans-serif;
  font-size: 14;
  padding-bottom: 5px;
}
#name {
  width: 50%;
  margin-right: 3%;
  border: whitesmoke;
  left: 50px;
  border-radius: 50px;
  color: black;
  background-color: rgba(0, 123, 255, 0.1);
}
#name:focus {
  border-color: #80bdff;
  outline: 0;
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}
.form_class {
  margin-top: 6vh;
  border: none;
  margin: 5pt;
  padding: 5pt;
  border-radius: 10px;
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  font-size: 1em;
}
.sort_name {
  border: none;
  padding: 2pt;
  text-align: center;
  border-radius: 10px;
  font-family: "Poppins", sans-serif;
  font-weight: 300;
  font-size: 1.2em;

  color: black;
}

.array-container {
  position: relative;
  length: 100%;
  top: 10px;
  margin: 10pt;
}

.array-bar,
.array-bar2 {
  width: 5pt;
  display: inline-block;
  margin: 0 1pt;
}

.button {
  margin-top: 6vh;
  border: none;
  margin: 10pt;
  padding: 10pt;
  border-radius: 10px;
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  font-size: 1em;
}

.button:focus {
  outline: none;
}
