@import url(https://fonts.googleapis.com/css?family=Poppins:200i,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#name-label {
  padding: 20px;
  padding-top: 10px;
  color: rgba(11, 11, 11, 0.8);
  font-family: "Poppins", sans-serif;
  font-size: 14;
  padding-bottom: 5px;
}
#name {
  width: 50%;
  margin-right: 3%;
  border: whitesmoke;
  left: 50px;
  border-radius: 50px;
  color: black;
  background-color: rgba(0, 123, 255, 0.1);
}
#name:focus {
  border-color: #80bdff;
  outline: 0;
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}
.form_class {
  margin-top: 6vh;
  border: none;
  margin: 5pt;
  padding: 5pt;
  border-radius: 10px;
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  font-size: 1em;
}
.sort_name {
  border: none;
  padding: 2pt;
  text-align: center;
  border-radius: 10px;
  font-family: "Poppins", sans-serif;
  font-weight: 300;
  font-size: 1.2em;

  color: black;
}

.array-container {
  position: relative;
  length: 100%;
  top: 10px;
  margin: 10pt;
}

.array-bar,
.array-bar2 {
  width: 5pt;
  display: inline-block;
  margin: 0 1pt;
}

.button {
  margin-top: 6vh;
  border: none;
  margin: 10pt;
  padding: 10pt;
  border-radius: 10px;
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  font-size: 1em;
}

.button:focus {
  outline: none;
}

.nav_links {
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  font-size: 1.2vw;
  color: rgb(216, 216, 216);
  padding-right: 2vw;
}

#logo {
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  font-size: 1.2vw;
  color: rgb(255, 100, 34);
  padding-left: 2vw;
  transition: all 0.3s ease 0s;
  text-decoration: none;
}

#logo small {
  padding-left: 1vw;

  display: none;
}

#logo:hover {
  color: rgb(255, 0, 0) !important;
}

#logo small:hover {
  display: inline-flex;
}

.nav_links li {
  display: inline-block;
  margin-top: 10px;
  padding: 0px 2vw;
}

.nav_links li a {
  color: rgb(216, 216, 216);
  text-decoration: none;
  transition: all 0.3s ease 0s;
}

.nav_links li a:hover {
  color: rgb(255, 55, 55) !important;
}

.navbar {
  z-index: -1;
  border-radius: 0% !important;
  border: none !important;
  padding: none !important;
  background-color: rgba(40, 40, 40, 1);
  position: relative;
  width: 100vw;
  height: 8vh;
  box-shadow: 1px 1px 4px 0px rgba(35, 35, 35, 1);
  display: flex;
  justify-content: start;
  align-items: center;
}

#divider {
  flex: 1 1;
}

#hamburger {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 30px;
  width: 30px;
  background: transparent;
  border: none;
  padding: 5px 5px;
  cursor: pointer;
  margin-right: 3vw;
}

.hamburger-line {
  border: none;
  padding: 0px 0px 0px 0px;
  margin: 0px 0px 0px 0px;
  width: 25px;
  height: 2px;
  background: white;
}
#hamburger:active {
  background: rgba(255, 255, 255, 0.25);
}

#hamburger {
  display: none;
}

#hamburger:focus {
  outline: none;
}

@media (max-width: 600px) {
  .nav_links li {
    display: none;
  }

  #divider {
    display: none;
  }

  .nav_links {
    display: none;
  }

  #logo {
    font-size: 1em;
    padding-left: 0vw;
    margin-right: 15vw;
    margin-left: 2vw;
  }

  #hamburger {
    display: flex;
    margin-left: 11vw;
  }
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.box {
  display: block;
  text-align: center;
  height: 500px;
  transition: all 0.3s ease 0s;
}

